@import url("https://fonts.googleapis.com/css?family=Palanquin:100&display=swap");
html .solarized {
  background-color: #002b36;
  color: #839496;
}
html .solarized * {
  color: #839496;
}
html .solarized h1, html .solarized h2, html .solarized h3, html .solarized h4, html .solarized h5, html .solarized h6 {
  color: #93a1a1;
  border-color: #839496;
}
html .solarized a, html .solarized a:active, html .solarized a:visited {
  color: #93a1a1;
}
html .solarized .header--name h1 {
  color: #eee8d5;
}
html .solarized .header--bio {
  color: #93a1a1;
}
html .solarized .links--single a, html .solarized .links--single a:visited {
  border-color: #839496;
  color: #268bd2;
}
html .solarized .links:hover, html .solarized .links:active {
  color: #eee8d5;
}

html .solarized-light {
  background-color: #fdf6e3;
  color: #657b83;
}
html .solarized-light * {
  color: #657b83;
}
html .solarized-light h1, html .solarized-light h2, html .solarized-light h3, html .solarized-light h4, html .solarized-light h5, html .solarized-light h6 {
  color: #586e75;
  border-color: #657b83;
}
html .solarized-light a, html .solarized-light a:active, html .solarized-light a:visited {
  color: #586e75;
}
html .solarized-light .header--name h1 {
  color: #002b36;
}
html .solarized-light .header--bio {
  color: #073642;
}
html .solarized-light .links--single a, html .solarized-light .links--single a:visited {
  border-color: #839496;
  color: #268bd2;
}
html .solarized-light .links--single a:hover, html .solarized-light .links--single a:active {
  color: #586e75;
}

html .solarized-violet {
  background-color: #6c71c4;
  background: linear-gradient(to right, #450494, #b892ff 75%, #2dc9bf);
}
html .solarized-violet .header--name h1, html .solarized-violet .header--bio, html .solarized-violet .header--pronouns {
  color: #fff;
}
html .solarized-violet .header--pronouns p {
  font-weight: 600;
  margin: 0;
}
html .solarized-violet .header--bio {
  margin: 0 auto;
  max-width: 600px;
}
html .solarized-violet .fas,
html .solarized-violet .fab,
html .solarized-violet .fa,
html .solarized-violet .far {
  color: #3cf73c;
}
html .solarized-violet .links--single a, html .solarized-violet .links--single a:visited {
  border-color: #00DBDE;
  color: #FEFEFE;
}
html .solarized-violet .links--single a:hover {
  color: #3cf73c;
  border-color: #3cf73c;
}
html .solarized-violet .links--single a:hover:before {
  color: #3cf73c;
}
html .solarized-violet .links--single a:focus {
  border: 2px dotted #00DBDE;
}

html * {
  color-profile: sRGB;
  rendering-intent: auto;
  font-family: "Palanquin", "Open Sans", sans-serif;
}

body a.fork-me {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  width: 150px;
  height: 100px;
  z-index: 1;
}

.wrap {
  margin: -8px;
  padding: 10px;
  height: 100vh;
  font-size: 1.1rem;
  text-align: center;
}
.wrap header {
  margin: 40px auto;
}
.wrap header img {
  border-radius: 50%;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.8);
  width: 150px;
}
.wrap header .header--name h1 {
  margin: 0;
}
.wrap header .header--pronouns {
  font-style: italic;
}
.wrap .links {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.wrap .links--single span {
  margin-right: 10px;
}
.wrap .links--single a {
  transition: color 0.25s linear;
}
.wrap .links--single a,
.wrap .links--single a:visited {
  text-decoration: none;
  border-bottom: 1px dotted;
}